import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { useEffect, useState, useReducer } from "react";
import HomePage from "../HomePage";

import SunPage from "../SunPage";
import sun from "../../planets-better/Sun.png";

import VenusPage from "../VenusPage";
import venus from "../../planets-better/Venus.png";

import MercuryPage from "../MercuryPage";
import mercury from "../../planets-better/Mercury.png";

import EarthPage from "../EarthPage";
import earth from "../../planets-better/Earth.png";

import MarsPage from "../MarsPage";
import mars from "../../planets-better/Mars.png";

import JupiterPage from "../JupiterPage";
import jupiter from "../../planets-better/Jupiter.png";

import SaturnPage from "../SaturnPage";
import saturn from "../../planets-better/Saturn.png";

import NeptunePage from "../NeptunePage";
import neptune from "../../planets-better/Neptune.png";

import UranusPage from "../UranusPage";
import uranus from "../../planets-better/Uranus.png";

import PlutoPage from "../PlutoPage";
import pluto from "../../planets-better/Pluto.png";

const gridAreas = ["a", "b", "c", "d", "e", "f", "g", "h", "i", "k"];
function goToMiddle(gridArea) {
  document
    .querySelectorAll(".content")
    .forEach((li) => li.classList.add("planetsLeave"));
  document.querySelector(`.${gridArea}`).classList.remove("planetsLeave");
  document.querySelector(`.${gridArea}`).classList.add("e");
  return;
}
function App() {
  const [memes, setMemes] = useState([]);
  useEffect(() => {
    async function getMemes() {
      const res = await fetch(`https://meme-api.herokuapp.com/gimme/9`);
      const data = await res.json();
      setMemes(data.memes);
    }
    getMemes();
  }, []);
  console.log(memes);

  return (
    <Router>
      <nav>
        <div id="grid-container">
          <li className="header">
            <Link to="/">
              <h1>INTERGALACTIC DANK MEMES</h1>
            </Link>
          </li>
          <li className="content e">
            <Link to="/sun">
              <img src={sun} alt="happy sun" />
            </Link>
          </li>
          <li
            className="content a"
            onClick={() => {
              goToMiddle("a");
            }}
          >
            <Link to="/venus">
              <img src={venus} alt="mars" />
            </Link>
          </li>
          <li className="content b">
            <Link to="/mercury">
              <img src={mercury} alt="mercury" />
            </Link>
          </li>
          <li className="content c">
            <Link to="/earth">
              <img src={earth} alt="happy earth" />
            </Link>
          </li>
          <li className="content d">
            <Link to="/mars">
              <img src={mars} alt="mars" />
            </Link>
          </li>
          <li className="content f">
            <Link to="/jupiter">
              <img src={jupiter} alt="jupiter" />
            </Link>
          </li>
          <li className="content g">
            <Link to="/saturn">
              <img src={saturn} alt="saturn" />
            </Link>
          </li>
          <li className="content h">
            <Link to="/uranus">
              <img src={uranus} alt="uranus" />
            </Link>
          </li>
          <li className="content i">
            <Link to="/neptune">
              <img src={neptune} alt="neptune" />
            </Link>
          </li>
          <li className="content k">
            <Link to="/pluto">
              <img src={pluto} alt="pluto" />
            </Link>
          </li>
        </div>
      </nav>
      <Switch>
        <Route path="/sun">
          <SunPage />
        </Route>
        <Route path="/venus">
          <VenusPage />
        </Route>
        <Route path="/mercury">
          <MercuryPage />
        </Route>
        <Route path="/earth">
          <EarthPage />
        </Route>
        <Route path="/mars">
          <MarsPage />
        </Route>
        <Route path="/jupiter">
          <JupiterPage />
        </Route>
        <Route path="/saturn">
          <SaturnPage />
        </Route>
        <Route path="/uranus">
          <UranusPage />
        </Route>
        <Route path="/neptune">
          <NeptunePage />
        </Route>
        <Route path="/pluto">
          <PlutoPage />
        </Route>
        <Route path="/">
          <HomePage />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
